@-webkit-keyframes RotateAW {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes RotateAW {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes RotateCW {
from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
to {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
}
@keyframes RotateCW {
from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
to {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
}
@-webkit-keyframes Circuit {
from {
    background: url(images/home/circuit__switch--01.png);
}
to {
    background: url(images/home/circuit__switch--02.png);
}
}
@keyframes Circuit {
from {
    background: url(images/home/circuit__switch--01.png);
}
to {
    background: url(images/home/circuit__switch--02.png);
}
}

/*顺逆时针转动动画*/
.RotateAW {
  -webkit-animation: RotateAW 5s linear infinite;
          animation: RotateAW 5s linear infinite;
}
.RotateCW {
  -webkit-animation: RotateCW 5s linear infinite;
          animation: RotateCW 5s linear infinite;
}
.paused {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.round {
  position: relative;
  width: 250px;
  height: 250px;
}
.round img {
    width: 250px;
    height: 250px;
}
.round__outside {
    position: absolute;
    top: 0;
}
.round__inside {
    position: absolute;
    top: 0;
}
.round__cent {
    position: absolute;
    top: 0;
}
.round__title {
    width: 125px;
    height: 125px;
    cursor: pointer;
    position: absolute;
    top: 63px;
    left: 63px;
    border-radius: 50%;
}
.round__title p {
      color: #b1dad9;
      text-align: center;
      line-height: 85px;
      font-size: 15px;
}
.round__title:hover {
    background-color: rgba(10, 218, 217, 0.3);
}
.header {
  height: 60px;
  width: 100%;
}
.header__logo {
    width: 30%;
    height: 100%;
    float: left;
    position: relative;
}
.header__logo__img {
      position: absolute;
      width: 30%;
      bottom: -20%;
      left: 23%;
}
.header__logo__bottom_1 {
      border-bottom: 1px solid #b1dad9;
      height: 100%;
      width: 20%;
      float: left;
}
.header__logo__bottom_2 {
      border-bottom: 1px solid #b1dad9;
      height: 100%;
      width: 45%;
      float: right;
}
.header__nav {
    width: 40%;
    height: 60px;
    float: left;
}
.header__nav span {
      display: block;
      color: white;
      text-align: center;
      line-height: 80px;
      font-size: 12px;
      color: #b1dad9;
}
.header__navBox {
    position: relative;
    width: 33.33%;
    height: 100%;
    float: left;
}
.header__navBox--BottomLine {
      border-bottom: 1px solid #b1dad9;
}
.header__navBox__oricl {
      position: absolute;
      z-index: 99;
      bottom: -1px;
      width: 100%;
      height: 45px;
}
.header__navBox__oricl img {
        width: inherit;
        height: inherit;
}
.header__right {
    width: 30%;
    height: 100%;
    float: left;
    position: relative;
    border-bottom: 1px solid #b1dad9;
}
.header__right__login {
      position: absolute;
      right: 20%;
}
.header__right__login a {
        text-align: center;
        line-height: 80px;
        font-size: 12px;
        color: rgba(177, 218, 217, 0.5);
}
.header__right__login a:hover {
          color: #b1dad9;
}
.header__right__login span {
        color: rgba(177, 218, 217, 0.5);
        font-size: 1.5em;
        font-weight: lighter;
}
.circuit {
  width: 347px;
  height: 447px;
  position: relative;
}
.circuit__cent {
    -webkit-animation: Circuit 2s linear infinite;
            animation: Circuit 2s linear infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    width: 100%;
    width: 347px;
    height: 447px;
    position: absolute;
    top: 0;
}
.circuit__left {
    width: 72px;
    height: 259px;
    position: absolute;
    bottom: -3px;
    left: -1.5%;
    background-image: url(images/home/circuit__left.png);
}
.circuit__right {
    width: 92px;
    height: 338px;
    position: absolute;
    bottom: 30px;
    right: 28px;
    background-image: url(images/home/circuit__right.png);
}
.gear img {
  width: 250px;
  height: 250px;
}
.button {
  outline: none;
  cursor: pointer;
  border: 2px solid rgba(177, 218, 217, 0.3);
  background-color: rgba(177, 218, 217, 0.08);
  color: rgba(177, 218, 217, 0.5);
}
.button--small {
    width: 70px;
    height: 30px;
    font-size: 12px;
}
.button--normal {
    width: 110px;
    height: 45px;
}
.button:hover {
    background-color: rgba(177, 218, 217, 0.3);
    color: #b1dad9;
}
.AddInput {
  width: 210px;
  height: 210px;
}
.AddInput img {
    width: inherit;
    height: inherit;
}
.AddInput__form {
    width: inherit;
    height: inherit;
    background-color: rgba(177, 218, 217, 0.08);
}
.AddInput__form__select {
      width: 90%;
      height: auto;
      padding: 20px 10px;
}
.AddTarget {
  width: 950px;
  height: 500px;
  position: relative;
}
.AddTarget img {
    width: 100%;
    height: 100%;
}
.AddTarget__AddInput {
    position: absolute;
    margin-left: 105px;
    height: 210px;
    width: 750px;
    top: 25%;
}
.AddTarget__AddInput__input {
      float: left;
      margin: 20px;
}
.AddTarget__Time {
    position: absolute;
    width: 400px;
    height: 50px;
    bottom: 50px;
    left: 130px;
}
.AddTarget__Time > span {
      text-align: center;
      line-height: 70px;
      color: rgba(177, 218, 217, 0.5);
}
.AddTarget__inputTimeLeft {
    position: absolute;
    bottom: 0;
    left: 150px;
}
.AddTarget__inputTimeRight {
    position: absolute;
    bottom: 0;
    left: 270px;
}
.AddTarget__button {
    position: relative;
    left: 45%;
    top: 10px;
}
.dropdown__title {
  color: #b1dad9;
  font-size: 12px;
  margin-bottom: 12px;
}
.dropdown__button {
  outline: none;
  border: none;
  font-size: 12px;
  cursor: pointer;
  height: 30px;
  width: auto;
  line-height: 30px;
  background-color: rgba(177, 218, 217, 0.08);
  color: rgba(177, 218, 217, 0.5);
}
.dropdown__button span {
    padding-left: 6px;
    padding-right: 6px;
}
.dropdown__icon {
  float: right;
}
.dropdown__icon img {
    width: 12px;
    height: 8px;
    padding: 12px;
}
.dropdown__list {
  position: absolute;
  z-index: 99;
  max-height: 200px;
  overflow: auto;
}
.dropdown__list li {
    list-style: none;
    font-size: 12px;
    cursor: pointer;
    height: 30px;
    line-height: 30px;
    padding-left: 5px;
    padding-right: 42px;
    padding-top: 5px;
    padding-bottom: 5px;
    outline: none;
    background-color: #34597B;
    color: rgba(177, 218, 217, 0.5);
}
.dropdown__list li:hover {
      background-color: #418196;
      color: #82FCFF;
}
#app {
  width: 100%;
  height: 100vh;
}
body {
  overflow: hidden;
}
.HomeBg {
  background: url("./images/bg.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  background-size: cover;
}
.outherBg {
  background: url("./images/bg02.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  background-size: cover;
}
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  /*transform: translateX(0)*/
}
.slide-fade-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.8s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-fade-enter, .slide-fade-leave-active {
  opacity: 0;
  /*transform: translateX(-100%)*/
}



